html,
body,
#root {
  height: 100%;
  width: 100%;
  overflow: hidden;
}

body {
  font-family: system-ui;
  margin: 0;
}

*,
*:after,
*:before {
  box-sizing: border-box;
}

.page {
  position: absolute;
  width: 100%;
  height: 100%;
  will-change: transform;
}

.page > div {
  touch-action: none;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  width: 100%;
  height: 100%;
  will-change: transform;
  box-shadow: 0 62.5px 125px -25px rgba(50, 50, 73, 0.5), 0 37.5px 75px -37.5px rgba(0, 0, 0, 0.6);
}

.container {
  display: flex;
  align-items: center;
  height: 100%;
  justify-content: center;
}

.wrapper {
  position: relative;
  width: 100%;
  height: 100%;
}

.text-wrapper {
  position: absolute;
  top: 0;
  text-align: center;
  width: 100vw;
  height: 100%;
  display: flex;
  align-items: center;
  padding: 10%;
}

.text {
  color: white;
  font-family: fantasy;
  font-size: 20px;
  line-height: 2;
}

@media only screen and (min-width: 500px) {
  .text-wrapper {
    padding: 10% 32%;
  }

  .page > div {
    background-size: contain;
  }
}